import { Card, CardHeader, CardContent } from "../../../../../components/Elements/cards/Card";
import { Badge } from "../../../../../components/Elements/Badge";
import { candidateTableData } from "@/lib/models/candidate";
import { jobCardData } from "@/lib/models/client";
import { useRouter } from "next/router";
import { useState } from "react";
import { CandidateCard } from "@/components/Elements/cards/candidateCard";
import MainLayout from "@/components/Layouts/layout";
import { Popup } from "@/components/Elements/cards/popup";
import { JobCard} from "@/components/Elements/cards/jobCard";
import { round } from "@/lib/models/candidate";
import InterviewForm from "@/components/Forms/updateInterview";
import AddRound from "@/components/Forms/addInterview";
export default function CandidateInterviews() {
  const router = useRouter();
  const Id = router.query.interviewid;
  const candidateId=router.query.id
  console.log(Id,candidateId);
  const [addRoundEnabled, setAddRoundEnabled] = useState(false);
  const [updateRoundEnabled, setUpdateRoundEnabled] = useState(false);
  return (
    <MainLayout>
      <div className="space-y-6">
        {/* Profile Info Section */}
        <section>
          <h2 className="text-xl font-semibold mb-4">Profile Info</h2>
          {/* Candidate Card needed Here */}
          <CandidateCard  candidateData={candidateTableData.content[Number(Id)]} />
        </section>

        {/* Job Info Section */}
        <section className="space-y-4">
          <h3 className="text-xl font-semibold">Job Info</h3>
          <JobCard jobData={jobCardData[Number(candidateId)-1]} isClient={false} />
        </section>

        {/* Interviews Section */}
        <section>
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold">Interviews</h2>
            <div className="flex items-center gap-2">
              <span className="text-gray-600 font-semibold">
                Over all status
              </span>
              <Badge variant="success">{round.interview.interviewStatus}</Badge>
            </div>
          </div>

          <div className="space-y-4">
            {/* Round 1 */}
            <Card className="p-4">
              <CardHeader className="mb-4">
                <h3 className="font-semibold text-lg">Round {round.roundNumber}</h3>
                <button
                  className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded text-base" onClick={() => setUpdateRoundEnabled(true)}>
                  Update
                </button>
              </CardHeader>
              {updateRoundEnabled && (
                <Popup onClose={() => setUpdateRoundEnabled(false)}>
                  <InterviewForm round={round} className="bg-white relative mt-12"/>
                </Popup>
              )}
              <CardContent className="bg-white border shadow-sm p-4">
                <div className="flex justify-between items-start mb-6">
                  <div className="flex items-center gap-12">
                    <div>
                      <h4 className="font-semibold text-lg">
                        {round.roundName}
                      </h4>
                      <p className="text-sm text-gray-500">{round.roundDate}</p>
                    </div>
                  </div>

                  <Badge variant="success">{round.interviewStatus}</Badge>
                </div>

                <div className="grid grid-cols-2 gap-6 md:grid-cols-4">
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Interviewer Name
                    </p>
                    <p className="text-gray-700">{round.interviewerName}</p>
                  </div>
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Technology Interviewed
                    </p>
                    <p className="text-gray-700">{round.technologyInterviewed}</p>
                  </div>
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Tech Rating
                    </p>
                    <p className="text-gray-700">{round.techRating}/10</p>
                  </div>
                  <div>
                    <p className="text-sm text-black font-semibold">
                      Soft skill Rating
                    </p>
                    <p className="text-gray-700">{round.softskillsRating}/10</p>
                  </div>
                </div>

                <div className="mt-4">
                  <p className="text-sm text-black font-semibold">Remarks</p>
                  <p className="text-gray-700">{round.remarks}</p>
                </div>
              </CardContent>
            </Card>

            {/* Round 2 and 3 would be similar to Round 1, so I'm omitting them for brevity */}

            <div className="flex justify-end mt-8">
              <button
                className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
                onClick={() => setAddRoundEnabled(true)}>
                Add New Round
              </button>
            </div>
          </div>
        </section>
        {addRoundEnabled && (
          <Popup onClose={() => setAddRoundEnabled(false)}>
              <AddRound className="mt-10 rounded-md bg-white m-8"></AddRound>
          </Popup>
        )}
      </div>
    </MainLayout>
  );
}
